body {
  margin: 0;
  padding: 0;
  background: #00ffff;

  font-family: 'Press Start 2P', cursive;
  font-style: normal;
  font-weight: normal;
  font-size: 48px;
  line-height: normal;
  text-align: center;

  color: white;

  text-shadow: -4px 0 black, 0 4px black, 4px 0 black, 0 -4px black;
}


#title {
  margin-top: 10vh;
  margin-bottom: 10vh;
  font-family: 'Press Start 2P', cursive;
  font-style: normal;
  font-weight: normal;
  font-size: 96px;
  line-height: normal;
  text-align: center;
  color: white;
  text-shadow: -4px 0 black, 0 4px black, 4px 0 black, 0 -4px black;
}

.center {
  text-align: center;
}

#greeter{
  font-size: 20px;
  position: absolute;
  top: 10px;
  left: 10px;
  text-shadow: -2px 0 black, 0 2px black, 2px 0 black, 0 -2px black;
}

#back-link {
  margin-top: 10vh;
}
.link{
  text-decoration: none;
  color: white;
}

.margin {
  margin: 2vh;
}
#cash{
    font-size: 20px;
    position: absolute;
    top: 10px;
    right: 10px;
    text-shadow: -2px 0 black, 0 2px black, 2px 0 black, 0 -2px black;
  }
.option {
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.danger {
    font-size: 36px;
    color: red;
}

.button-text {
    font-size: 20px;
    text-shadow: -2px 0 black, 0 2px black, 2px 0 black, 0 -2px black;
}

.dialog-text {
    margin-top: 5px;
    font-size: 26px;
    text-shadow: -2px 0 black, 0 2px black, 2px 0 black, 0 -2px black;
    text-align: left;
}

.input {
    margin-top: 10px;
    border-top: 0;
    border-left: 0;
    border-bottom: 5px solid black;
    border-right: 0;
    font-family: 'Press Start 2P', cursive;
    font-style: normal;
    font-weight: normal;
    line-height: normal;
    font-size: 26px;
    color: white;
    text-shadow: -2px 0 black, 0 2px black, 2px 0 black, 0 -2px black;
    caret-color: black;
    line-height: 32px;
}
